.list-item {
  width: 100%;
  display: flex;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
  box-shadow: 0px 1px 3px 0px rgba(0,0,0,0.2), 0px 1px 1px 0px rgba(0,0,0,0.14), 0px 2px 1px -1px rgba(0,0,0,0.12);
  overflow: hidden;
  text-overflow: ellipsis;
  margin-top: 10px;
  color: #000;
  text-decoration: none; }
